<template>
<div style="">
    <div style="width:100%;height:50px;margin-bottom:20px;">
        <div style="width:12%;display:inline-block;float:left">
            <el-input v-model="inputCph" placeholder="请输入车牌号" ></el-input>
        </div>
        <!-- 支付状态查询 --> 
          <div style="display:inline-block;float:left;font-size:16px;line-height: 40px;margin-left:5%">
             <span>支付状态&nbsp;&nbsp;</span> 
             <el-select v-model='zfVal' placeholder="请选择" style='max-width:120px;'>
                <el-option
                 v-for='item in zfOptions'
                 :key='item.id'
                 :label='item.label'
                 :value='item.value'>
                </el-option>
             </el-select>  
          </div>
        <!-- 过车时间-区间查询 -->
        <div style="display:inline-block;float:left;font-size:16px;line-height: 40px;margin-left:5%">
            <span>过车时间&nbsp;&nbsp;</span> 
                <el-date-picker
                  v-model="tiemVal"
                  type="datetimerange"
                  align="right"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  :default-time="['12:00:00', '08:00:00']">
                </el-date-picker>
         </div>

        <div style="float:left;margin-left:20px;">
          <el-button type="primary" @click="getCarMsg()">查询</el-button>
        </div>
    </div>
    <!-- 第二个表格 -->
 <el-table
      :data="tableCar"
      style="width: 100%;">
      
      <el-table-column
        prop="id"
        label="id"
        width="80">
      </el-table-column>
      <el-table-column
        prop="plateColor"
        label="车型"
        width="80">
      </el-table-column>
      <el-table-column
        prop="plateNum"
        label="车牌号">
      </el-table-column>
      <el-table-column
        prop="hasPayed"
        label="支付状态">
      </el-table-column>
      <el-table-column
        prop="serverName"
        label="站点名称">
      </el-table-column>
      <el-table-column
        prop="passingTime"
        label="过车时间"
        width='200'>
      </el-table-column>
      <el-table-column
        prop="rfid"
        label="账号">
      </el-table-column>
      <!-- 操作 -->
      <el-table-column
      label="操作">
      <template slot-scope="scope">
          <el-button
              @click="handleDetailMsg(scope.$index, scope.row,scope)"
              type="primary"
              size="small">详情
          </el-button>
      </template>
      </el-table-column>
    </el-table> 
    <!-- 分页条 -->
    <!-- page-sizes, 每页显示条数   
        :page-size 当前页显示条数   feeSystemTradeNo
     -->
    <el-pagination
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="total" v-show='paginationShow' 
        style='margin-top:10px;'
        :page-sizes="[5,10,20,30]"      
        :page-size="pageSize"
        @size-change="handleSizeChange"
        @current-change="currentChange"
        :current-page="currentPage">
    </el-pagination>
    <!-- 模态框 -->
    <el-dialog title="详细信息" 
    :visible.sync="dialogVisible" customClass='customWidth' 
    :modal-append-to-body="false">

    <div style='height:60vh;position:relative;width:100%;' id='detailBox'>
      <div style="height:100%;">   
        <div class='fl' style="width:58%;height:80%;font-size:24px;">
          <div style="width:60%;height:60%;margin:0 auto" > 
              <img :src="imgRul2" preview='preview' style="width:480%;height:100%;z-index:5" id='imgMsg' alt='暂无图片'>
          </div>
        </div>
         <!-- 右侧部分   交易时间-->
          <div class='fr' style="width:40%;height:50%;font-size:24px;">
            <div class='bd'>
              <div class='fl'>id:&nbsp;&nbsp;</div>
              <div class='fl'>{{row.id}}</div>
          </div>
          <div class='bd'>
            <div class='fl'>支付时间:&nbsp;&nbsp;</div>
            <div class='fl'>{{row.paymentPayTime}}</div>
          </div>
          <div class='bd'>
            <div class='fl'>车型:</div>
            <div class='fl marginLfet'>{{row.plateColor}}</div>
          </div>
          <div class='bd'>
            <div class='fl'>支付状态:</div>
            <div class='fl marginLfet'>{{row.hasPayed}}</div>
          </div>
        <div class='bd'>
            <div class='fl'>车牌号:</div>
            <div class='fl marginLfet'>{{row.plateNum}}</div>
        </div>
        <div class='bd'>
          <div class='fl'>支付金额:</div>
          <div class='fl marginLfet'>{{row.paymentTollFee}}</div>
      </div>

          </div>
      </div>
      
    </div>
    </el-dialog>


</div>
</template>
<script>
  import moment from 'moment'
  import qs from 'qs'
  export default {
    methods: {
      handleClick(row) {
      },
      getTime(){
        if(!this.tiemVal){
          this.$message({
            message:"请选择过车时间!",
            type:"warning"
          })
          return;
        }
      },
      currentChange(currentPage){
          this.getTime();
          var self=this;
          this.currentPage=currentPage;
            var url='http://58.210.46.54:8000/listGcjlData';
            var paramString={
              'start':self.momentTime(this.tiemVal[0]),
              'end':self.momentTime(this.tiemVal[1]),
              'hasPayed':self.zfVal,
              'plateNum':this.inputCph,
              'current':this.currentPage,
              'size':this.pageSize
            }
            var jsonParams=JSON.stringify(paramString);
          this.axiosMsg(url,jsonParams);
      },
      handleSizeChange(val){
          this.pageSize=val;
          var self=this;
          this.getTime();
          var url='http://58.210.46.54:8000/listGcjlData';
          var str={
              'start':self.momentTime(this.tiemVal[0]),
              'end':self.momentTime(this.tiemVal[1]),
              'hasPayed':self.zfVal,
              'plateNum':this.inputCph,
              'current':this.currentPage,
              'size':val
          }
          var jsonParams=JSON.stringify(str);
           this.axiosMsg(url,jsonParams);
          console.log(`每页显示${val}条`)
      },
      axiosMsg(url,params){
        var self=this;
        this.paginationShow=true;
        this.$axios({
          method:"post",
          url:url,
          headers:{
            "Content-Type":"application/json"
          },
          data:params
        }).then(function(response){
          self.total=response.data.data.total;
          self.tableCar=response.data.data.records;
              for(var i=0;i<response.data.data.records.length;i++){
                  if(!self.tableCar[i].id){
                    self.tableCar[i].id='XX';
                  }
                  if(!self.tableCar[i].plateColor){
                    self.tableCar[i].plateColor='XX'
                  }
                      if(self.tableCar[i].plateColor.toLocaleUpperCase()){
                        self.tableCar[i].plateColor='蓝牌'
                      }
                  
                  if(!self.tableCar[i].plateNum){
                    self.tableCar[i].plateNum='XXX'
                  }
                  if(self.tableCar[i].paymentPayTime){
                    self.tableCar[i].paymentPayTime=new Date(self.tableCar[i].paymentPayTime).toLocaleString();
                  }else{
                    self.tableCar[i].paymentPayTime='';
                  }
                  if(self.tableCar[i].hasPayed=='true'){
                    self.tableCar[i].hasPayed='已支付'
                  }else if(self.tableCar[i].hasPayed=='false'){
                    self.tableCar[i].hasPayed='未支付'
                  }
                  if(!self.tableCar[i].stationId){
                    self.tableCar[i].stationId='XX'
                  }

                  if(!self.tableCar[i].rfid){
                    self.tableCar[i].rfid='XX'
                  }
                    if(self.tableCar[i].pay==1){
                      self.tableCar[i].pay="已支付"
                  }else{
                    self.tableCar[i].pay="未支付"
                  }
                  //添加过车时间
                  if(self.tableCar[i].passingTime){
                    self.tableCar[i].passingTime=new Date(self.tableCar[i].passingTime).toLocaleString();
                  }else if(!self.tableCar[i].passingTime){
                    self.tableCar[i].passingTime='';
                  }
              }
              console.log(self.tableCar);
        }).catch(function(err){
          console.log(err);
        })
      },
      momentTime(val){
          return moment(val).format("YYYY-MM-DD HH:mm:ss");
      },
      getCarMsg(){
        var self=this;
        this.axiosMsg();
          var url='http://58.210.46.54:8000/listGcjlData'
          // var url='http://58.210.46.54:8000/listGcjlData2?start='+this.momentTime(this.tiemVal[0])+"&end="+this.momentTime(this.tiemVal[1])+"&hasPayed="+this.zfVal+"&plateNum="+this.inputCph;
          if(!this.zfVal){
            this.$message({
                message:"请选择支付状态!",
                type:"warning"
            })
            return;
          }
          //查询，获取参数
          var str={
            'start':self.momentTime(this.tiemVal[0]),
              'end':self.momentTime(this.tiemVal[1]),
              'hasPayed':self.zfVal,
              'plateNum':this.inputCph,
          }
          var jsonParams=JSON.stringify(str);
          this.axiosMsg(url,jsonParams);
      },
      handleDetailMsg(index,row,scope){
        console.log(row);
        this.dialogVisible=true;
        this.row=row;
        //获取图片信息
        var url='http://58.210.46.54:8000/imageReq?serverId='+row.serverId+"&imageId="+row.imgId;
         this.$axios.get(url).then(response=>{
          this.imgRul2="data:image/jpg;base64,"+response.data.data.full_image;
          this.preview=row.id;
         })
      },
    },
    data() {
      return {
          inputCph:'',
          paginationShow:false,
          tableShow:[],
          tableCar:[],
          total:0,
          currentPage:2,
          pageSize:10,
          dialogVisible:false,
          row:Object,
          imgRul2:'',
          zfVal:'',
          zfOptions:[
            {id:1,label:"已支付",value:"true"},
            {id:2,label:"未支付",value:"false"}
          ],
          tiemVal:''
      }
    },
    mounted:function(){
        // this.getCarMsg();
    }
  }
</script>
<style scoped>
.customWidth{
  /* width:10%; */
}
.fl{
  float:left;
}.fr{
  float:right;
}
.bd{
  height:14%;
  font-size:0.8em;
  line-height:1em;
  margin-bottom:10px;
}
.marL{
  margin-right:20%;
}
.width{
  width:30%
}
.width2{
  width:30%
}
.textLeft{
  text-align: left;
}
</style>


